import React, { Component } from 'react'
import { Row, Button, ToggleButtonGroup, ToggleButton } from 'react-bootstrap'
class GamePanel extends Component{
    constructor(props){
        super(props);
        this.state={
            player:1
        }
        this.handleChange=this.handleChange.bind(this)
    }
    handleChange(event){
        // const {name,value}=event.target
        // this.setState({
        //     [name]:value
        // })
        this.props.handleChange(event)
    }
    render()
    {   
        return(
            <>
            
            <div className="GamePanel .d-none " id="GamePanel">
                <div className="Panel" id="Panel">
                    <Row>
                        <Button variant="outline-info" size="md" className="panelText panelElement" style={{marginBottom:"5px"}} onClick={this.props.NewGame} >New Game</Button>
                        <ToggleButtonGroup type="radio" name="player" size="md"className="panelElement" >
                            <ToggleButton variant="outline-info" value={1} onChange={this.props.handleChange} checked={this.props.board.player===1} >Single Player</ToggleButton>
                            <ToggleButton variant="outline-info" value={2} onChange={this.props.handleChange} checked={this.props.board.player===2}>Multi Player</ToggleButton>
                        </ToggleButtonGroup>
                    </Row>
                    {this.props.board.player==1?
                        <div>
                            <Row>
                                <p className="panelText panelElement">Difficulty</p>   
                            </Row>
                            <Row>
                                <ToggleButtonGroup type="radio" name="depth" size="md" className="panelElement">
                                    <ToggleButton variant="outline-info" value={1} onChange={this.props.handleChange} checked={this.props.board.depth==1}>1</ToggleButton>
                                    <ToggleButton variant="outline-info" value={2} onChange={this.props.handleChange} checked={this.props.board.depth==2}>2</ToggleButton>
                                    <ToggleButton variant="outline-info" value={3} onChange={this.props.handleChange} checked={this.props.board.depth==3}>3</ToggleButton>
                                    <ToggleButton variant="outline-info" value={4} onChange={this.props.handleChange} checked={this.props.board.depth==4}>4</ToggleButton>
                                    <ToggleButton variant="outline-info" value={9} onChange={this.props.handleChange} checked={this.props.board.depth==9}>Maximum</ToggleButton>
                                </ToggleButtonGroup>
                            </Row>
                            <Row>
                                <p className="panelText panelElement">Who starts?</p>
                            </Row>
                            <Row>
                                <ToggleButtonGroup type="radio" name="nextSymbol" size="md" className="panelElement" >
                                    <ToggleButton variant="outline-info" value="O" onChange={this.props.handleChange} checked={this.props.board.nextSymbol=="O"}>Human</ToggleButton>
                                    <ToggleButton variant="outline-info" value="X" onChange={this.props.handleChange} checked={this.props.board.nextSymbol=="X"}>Computer</ToggleButton>
                                </ToggleButtonGroup>
                            </Row>
                        </div>:<></>
                    }
                    <Row>
                        <Button variant="outline-info" size="md" className="panelElement panelText" onClick={this.props.showHint} style={{marginTop:"5px"}}>Hint</Button>
                    </Row>
                </div>
            </div>
            </>
        )
    }
}
export default GamePanel